import { Component, OnInit } from '@angular/core';
import * as firebase from 'firebase/app';
import 'firebase/auth';

import {FormBuilder, FormControl ,FormGroup , Validators} from '@angular/forms';
@Component({
  selector: 'app-sign-up-form',
  templateUrl: './sign-up-form.component.html',
  styleUrls: ['./sign-up-form.component.css']
})
export class SignUpFormComponent implements OnInit {

  myForm : FormGroup ;
  constructor( public fb :FormBuilder) { 
    this.myForm = this.fb.group(
      {
        firstName : ['' ,[ Validators.required]]  ,
        lastName : ['' ,[Validators.required]],
        email : ['' ,[Validators.required ]],
        password : ['' ,[Validators.required ,Validators.minLength(8)]],
        confirm_password :['' ,[Validators.required]]
      },
      {
        validator : this.checkmatchingpassword("password","confirm_password")
      }
    )
  }
  checkmatchingpassword(passwordkey : string,confirm_passwordkey : string)
  {
    return (group : FormGroup) =>
    {
      let password = group.controls[passwordkey];
      let confirm_password = group.controls[confirm_passwordkey];

      if(password.value==confirm_password.value)
      {
        return;
      }
      else
      {
        confirm_password.setErrors(
          {
            notequaltoPassword : true
          }
        )
      }



    }
  }
  onsubmit(signupform)
  {
     let email : string = signupform.value.email;
     let password : string = signupform.value.password;
     let firstName : string = signupform.value.firstName;
     let lastName : string = signupform.value.lastName;

     firebase.auth().createUserWithEmailAndPassword(email,password).then((response)=>{
     console.log(response);
      response.user.updateProfile(
        {
          displayName : firstName +" "+lastName
        }
      )   })
     .catch((error)=>{
       console.log(error);
     })
  }

  ngOnInit() {
  }

}
